	vertical-align: baseline;
	font-size: 100%;
	font-family: 'Spartan', sans-serif;
}

/* ====================
 * GENERALS STYLES
 * ==================== */

div {
	min-width: 0;
	overflow-wrap: break-word;
}

img {
	vertical-align: top;
	object-fit: contain;
	object-position: center center;
	font-size: 1rem;
	text-align: center;
}

:is(button, select, input, input:disabled) {
	appearance: none;
	-webkit-appearance: none;
}

hr {
	box-sizing: content-box;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

video[autoplay]:not([muted]) {
	display: none;
