:is(:root, :root[data-theme='neutral']) {
	--radius: 12px;
	--white-space: 20px;
	--color-background: hsl(222, 26%, 31%);
	--color-track: hsl(223, 31%, 20%);
	--color-thumb: hsl(6, 63%, 50%);
	--color-font-theme: #ffffff;
	--color-font-123: #ffffff;
	--color-screen: hsl(224, 36%, 15%);
	--color-value: #ffffff;
	--color-keypad: hsl(224, 32%, 20%);
	--color-button-number: hsl(30, 25%, 89%);
	--color-shadow-button-number: hsl(28, 16%, 65%);
	--color-font-button-number: hsl(221, 14%, 31%);
	--color-button-del: hsl(225, 21%, 49%);
	--color-shadow-button-del: hsl(224, 28%, 35%);
	--color-font-button-del: #ffffff;
	--color-button-reset: hsl(225, 21%, 49%);
	--color-shadow-button-reset: hsl(224, 28%, 35%);
	--color-font-button-reset: #ffffff;
	--color-button-result: hsl(6, 63%, 50%);
	--color-shadow-button-result: hsl(6, 70%, 34%);
	--color-font-button-result: #ffffff;
}

:root[data-theme='dark'] {
	--radius: 12px;
	--white-space: 20px;
	--color-background: hsl(268, 75%, 9%);
	--color-track: hsl(268, 71%, 12%);
	--color-thumb: hsl(176, 100%, 44%);
	--color-font-theme: hsl(52, 100%, 62%);
	--color-font-123: hsl(52, 100%, 62%);
	--color-screen: hsl(267, 77%, 12%);
	--color-value: hsl(52, 100%, 62%);
	--color-keypad: hsl(269, 76%, 11%);
	--color-button-number: hsl(269, 50%, 20%);
	--color-shadow-button-number: #752893;
	--color-font-button-number: hsl(52, 100%, 62%);
	--color-button-del: hsl(281, 89%, 26%);
	--color-shadow-button-del: hsl(285, 91%, 52%);
	--color-font-button-del: #ffffff;
	--color-button-reset: hsl(281, 89%, 26%);
	--color-shadow-button-reset: hsl(285, 91%, 52%);
	--color-font-button-reset: #ffffff;
	--color-button-result: hsl(176, 100%, 44%);
	--color-shadow-button-result: hsl(177, 92%, 70%);
	--color-font-button-result: hsl(40, 6%, 21%);
}

:root[data-theme='light'] {
	--radius: 12px;
	--white-space: 20px;
	--color-background: hsl(0, 0%, 90%);
	--color-track: hsl(0, 5%, 81%);
	--color-thumb: hsl(25, 98%, 40%);
	--color-font-theme: #000000;
	--color-font-123: #000000;
	--color-screen: hsl(0, 0%, 93%);
	--color-value: #000000;
	--color-keypad: hsl(0, 7%, 81%);
	--color-button-number: hsl(45, 7%, 89%);
	--color-shadow-button-number: hsl(35, 11%, 61%);
	--color-font-button-number: hsl(60, 10%, 19%);
	--color-button-del: hsl(185, 42%, 37%);
	--color-shadow-button-del: hsl(185, 58%, 25%);
	--color-font-button-del: #ffffff;
	--color-button-reset: hsl(185, 42%, 37%);
	--color-shadow-button-reset: hsl(185, 58%, 25%);
	--color-font-button-reset: #ffffff;
	--color-button-result: hsl(25, 98%, 40%);
	--color-shadow-button-result: hsl(25, 99%, 27%);
	--color-font-button-result: #ffffff;
}

html {
	box-sizing: border-box;
	background: var(--color-background);
	scroll-behavior: smooth;
	-webkit-tap-highlight-color: transparent;
}

body {
	width: 100%;
	height: auto;
	min-height: 100vh;
	background-color: transparent;
	font-size: 16px;
	padding: 0 var(--white-space);
}

div[id='root'] {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: auto;
	min-height: 100vh;
	background-color: transparent;
}

.calculator {
	width: 400px;
}

.wrapper-header {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 3rem;
	margin-bottom: 1rem;
}

.screen {
	/* Wrapper */
	display: flex;
	justify-content: flex-end;
	align-items: center;
	width: 100%;
	height: 7rem;
	background-color: var(--color-screen);
	border-radius: var(--radius);
	padding: var(--white-space);
	margin-bottom: 1rem;
	overflow-x: hidden;

	/* Values */
	color: var(--color-value);
	font-size: 32px;
}

.keypad {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(5, 1fr);
	grid-column-gap: 20px;
	grid-row-gap: 20px;
	width: 100%;
	background-color: var(--color-keypad);
	border-radius: var(--radius);
	padding: var(--white-space);
}

/* Theme Slider */

input[type='range'] {
	position: relative;
	-webkit-appearance: none;
	width: 55px;
	background-color: transparent;
}

input[type='range']::before {
	position: absolute;
	content: 'THEME';
	top: 5px;
	right: 130%;
	font-size: 12px;
	color: var(--color-font-theme);
}

input[type='range']::after {
	position: absolute;
	content: '1 \00a0 \00a0 2 \00a0 \00a0 3';
	top: -22px;
	right: 0;
	width: 100%;
	text-align: center;
	font-size: 12px;
	color: var(--color-font-123);
}

input[type='range']::-webkit-slider-runnable-track {
	width: 100%;
	height: 22px;
	cursor: pointer;
	background: var(--color-track);
	border-radius: 30rem;
	padding: 0 5px;
}

input[type='range']::-webkit-slider-thumb {
	margin-top: 4px;
	height: 14px;
	width: 14px;
	border-radius: 50%;
	background-color: var(--color-thumb);
	cursor: pointer;
	-webkit-appearance: none;
}

/* Header :: Logotype */

.logotype {
	color: var(--color-font-theme);
	margin: 0;
	font-size: 26px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

/* Number Button */

.button {
	display: grid;
	place-items: center;
	height: 45px;
	border-radius: 8px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	padding-top: 4px; /* Visual Fix */
	transition: box-shadow 90ms ease, -webkit-transform 90ms ease;
	transition: box-shadow 90ms ease, transform 90ms ease;
	transition: box-shadow 90ms ease, transform 90ms ease, -webkit-transform 90ms ease;
}

.big {
	grid-column: span 2;
}

.button:active {
	-webkit-transform: translateY(4px);
	        transform: translateY(4px);
	box-shadow: none;
}

.number,
.operation {
	color: var(--color-font-button-number);
	font-size: 22px;
	font-weight: 600;
	background-color: var(--color-button-number);
	box-shadow: 0 4px 0 0 var(--color-shadow-button-number);
}

.delete {
	color: var(--color-font-button-del);
	font-size: 14px;
	font-weight: 600;
	text-transform: uppercase;
	background-color: var(--color-button-del);
	box-shadow: 0 4px 0 0 var(--color-shadow-button-del);
}

.reset {
	color: var(--color-font-button-reset);
	font-size: 14px;
	font-weight: 600;
	text-transform: uppercase;
	background-color: var(--color-button-reset);
	box-shadow: 0 4px 0 0 var(--color-shadow-button-reset);
}

.result {
	color: var(--color-font-button-result);
	font-size: 16px;
	font-weight: 600;
	background-color: var(--color-button-result);
	box-shadow: 0 4px 0 0 var(--color-shadow-button-result);
}


@media screen and (max-width: 445px) {
	.calculator {
		width: 100%;
		min-width: 330px;
	}
}



